 @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

*{
  font-family: 'Roboto', sans-serif;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #29ada1;
}
.head{
  
    color: #262834;
    display: block;
    position: absolute;
    text-align: center;
    transform: translate(-50%,-50%);
    top: 8%;
    left: 50%;
    margin: 0 auto;

}
.container{
  top: 50%;
  left: 50;
  position: absolute;
  transform: translate(-50%,-50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 5px;
  font-size: 18px;

}

.keypad{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(60px,auto);
  font-size: 18px;

}
input[type="text"]{
  height: 75px;
  width: 250px;
  background-color: #10111600;
  border: 0px;
  color: #ffffff;
  text-align: right;
  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 18px;

}
#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
  font-size: 18px;

}
#result{
  grid-column: 3/5;
  grid-row: 5;
  color: #262834;
  font-size: 18px;

}
#backspace{
  color: #262834;
  font-size: 18px;

}
button{
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;

}
input:focus,
input:active,
button:focus,
button:active{
  outline: none;
  font-size: 18px;

}
.highlight{
  background: #0b3e41;
  font-size: 18px;

}
.highlight2{
  background: #2ab2a5;
  font-size: 18px;

}